<template>
  <div id="about">
    <h3>简介</h3>
    <a>{{ name }}</a>
    <h3>联系方式</h3>
    <span>邮箱：</span><a class='underLine'>{{ mail_2 }}</a>
  </div>
</template>
<script>
import { store } from '../store/store'
export default {
  store: store,
  name: 'BlogAbout',
  data () {
    return {
      msg: ' ---- ',
      name: '马骁博',
      mail_1: 'maixaobo2015@gmail.com',
      mail_2: 'maixaobo2015@163.com',
      carrer: '前端攻城狮~汪汪'
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.$store.state.moduleAbout.domHeight = document.querySelectorAll('#about')[0].clientHeight
    })
  },
  methods: {
  }
}
</script>
<style lang='less' scoped>
@import '../less/varables.less';
#about {
  text-align: left;
  margin-left: 2em;

}
.underLine {
  text-decoration-line: underline;
  cursor: pointer;
}
@media screen and (max-device-width: 600px){
  #about {
    text-align: left;
    margin:0 auto;
    width: 80vw;
  }
}
</style>



