<template>
  <div id="articleContent">
    <ul>
      <li class="articleItem" v-for="article in articles" v-bind:todo="article" v-bind:key="article.id" @click="toArticle(article)">
        <div class="articleTitle">{{ article.title }}</div>
        <!-- <div class="articleAbstract">{{ article.abstract }}</div> -->
      </li>
    </ul>
    <div v-if="loading" class='loading'>
      <img src="../assets/20180615/lemon.svg" class='lemonBounce'/>
        Loading.....
    </div>
  </div>
</template>
<script>
import { store } from '../store/store'
import axios from 'axios'

export default {
  store: store,
  name: 'BlogArticle',
  data () {
    return {
      articles: [],
      loading: true
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.getArticles()
    })
  },
  methods: {
    getArticles: function () {
      const accessToken = '631d9ea26cf914c41f6f52' + 'a036cdffcc3e7a3706'
      this.loading = true
      axios.get('https://api.github.com/repos/MichaelMa666/MichaelMa666.github.io/issues?access_token=' + accessToken)
        .then((response) => {
          this.loading = false
          this.articles = response.data
        }, (error) => {
          console.log(error)
          this.$router.push({path: '/error403'})
          this.loading = false
        })
    },
    toArticle: function (obj) {
      this.$router.push({path: '/article/' + obj.number})
      this.$store.state.moduleArticle.num = obj.number
      this.$store.state.moduleArticle.content = obj.body
      this.$store.state.moduleArticle.title = obj.title
      this.$store.state.moduleArticle.updateTime = obj.updated_at
    }
  }
}
</script>
<style lang='less' scoped>
@import '../less/varables.less';
  ul{
    margin:0 auto;
  }
  .articleItem {
    display:flex;
    padding:1em;
    margin-bottom:1em;
    background-color:#FFF;
    cursor:pointer;
    transition: all .15s ease-out;
    text-align: left;
  }
</style>



