<template>
  <div id="headNav">
    <ul class='headMenu'>
      <li>
        <router-link to="/article"><i></i>{{ article }}</router-link>
      </li>
      <li>
        <router-link to="/gallary"><i></i>{{ gallary }}</router-link>
      </li>
      <li>
        <router-link to="/about"><i></i>{{ about }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'BlogHeadNav',
  data () {
    return {
      article: 'Study Note',
      gallary: 'Gallary',
      about: 'About Me'
    }
  }
}
</script>
<style lang='less' scoped>
@import '../less/varables.less';
  a{
    line-height: 1;
  }
  a i{
    width:1.2em;
    cursor:pointer;
  }
  ul {
    margin:0;
    text-align: left;
  }
  #headNav{
    width: 25em;
    position:fixed;
  }
  .headMenu li {
    position: relative;
    padding: 1.7em 2em;
    margin: 0;
  }
  @media screen and (max-device-width: 320px) {
    .headMenu li {
      position: relative;
      padding: 1.7em 1.5em;
      margin: 0;
    }
  }
  .headMenu li::before {
    content: '/';
    color: #42b983;
    position: absolute;
    left: 0;
  }
  .headMenu li:first-child:before {
    display: none;
  }
</style>



